@charset "UTF-8";
/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after {
  content: "";
  content: none;
}

q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

body {
  font-size: 14px;
}

@font-face {
  font-family: "Open Sans";
  src: url("development/fonts/Open_Sans/OpenSans-Bold.woff2") format("woff2"), url("development/fonts/Open_Sans/OpenSans-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: url("development/fonts/Open_Sans/OpenSans-Regular.woff2") format("woff2"), url("development/fonts/Open_Sans/OpenSans-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: url("development/fonts/Open_Sans/OpenSans-Light.woff2") format("woff2"), url("development/fonts/Open_Sans/OpenSans-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Charmonman";
  src: url("development/fonts/Charmonman/Charmonman-Bold.woff2") format("woff2"), url("development/fonts/Charmonman/Charmonman-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Charmonman";
  src: url("development/fonts/Charmonman/Charmonman-Regular.woff2") format("woff2"), url("development/fonts/Charmonman/Charmonman-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
.container {
  margin: 0 auto;
  max-width: 1244px;
}

.logo {
  font-family: "Charmonman", sans-serif;
  font-size: 2.5rem;
  padding: 30px;
}
.logo .logo--first {
  font-weight: 100;
  color: #FFFFFA;
}
.logo .logo--second {
  font-weight: 700;
  color: #FF6600;
}

.header {
  background: #4a4a49;
  font-family: "Open Sans", sans-serif;
}
.header .header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header .header__wrapper .header__nav a {
  text-decoration: none;
  text-transform: uppercase;
  color: #FFFFFA;
  padding: 20px;
}
.header .header__wrapper .header__nav a:hover {
  color: #FF6600;
}

#app--info {
  display: flex;
  justify-content: center;
  align-items: center;
}

.info__wrapper {
  display: flex;
  font-family: "Open Sans", sans-serif;
  background-color: #FFFFFA;
  color: #5B605F;
  padding: 70px 0;
}
.info__wrapper div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 15px 35px;
}
.info__wrapper div i {
  font-size: 5.5rem;
  color: #4a4a49;
  opacity: 0.5;
}
.info__wrapper div h2 {
  display: block;
  font-size: 2.2rem;
  text-align: center;
  margin: 35px 0 25px 0;
}
.info__wrapper div p {
  display: block;
  text-align: center;
  font-size: 1.7rem;
}

.about_author {
  display: flex;
  justify-content: center;
  background-color: #FFFFFA;
}
.about_author .container {
  display: flex;
  justify-content: space-between;
  min-width: 1244px;
  padding: 80px 0;
}
.about_author__avatar {
  margin-left: 70px;
  height: 350px;
  width: 350px;
  background-color: #5B605F;
}
.about_author__description {
  margin-right: 60px;
  width: 650px;
  font-family: "Open Sans", sans-serif;
  color: #4a4a49;
}
.about_author__description__title {
  margin-bottom: 25px;
  font-size: 2rem;
  font-weight: bold;
}
.about_author__description__text {
  font-size: 1.2rem;
  line-height: 25px;
}

footer {
  background-color: #5B605F;
}
footer .container .wrapper {
  display: flex;
  justify-content: center;
  gap: 40px;
}
footer .container .wrapper .footer__article {
  display: flex;
  flex-direction: column;
  font-family: "Open Sans", sans-serif;
  min-height: 200px;
  width: 20%;
  color: #FFFFFA;
}
footer .container .wrapper .footer__article h1 {
  font-size: 1.225rem;
  margin-top: 30px;
  margin-bottom: 20px;
}
footer .container .wrapper .footer__article p {
  font-size: 1rem;
}
footer .container .wrapper .footer__article .footer__article__form {
  display: flex;
}
footer .container .wrapper .footer__article .footer__article__form input {
  display: block;
  width: 50%;
  line-height: 1.225rem;
  border: none;
}
footer .container .wrapper .footer__article .footer__article__form input:focus {
  outline: none;
}
footer .container .wrapper .footer__article .footer__article__form .btn {
  display: block;
  background-color: #FF6600;
  padding-left: 15px;
  padding-right: 15px;
  font-weight: bold;
  color: #FFFFFA;
  text-align: center;
  line-height: 30px;
  text-decoration: none;
  width: 30%;
  cursor: pointer;
}
footer .container .wrapper .footer__article .footer__article-ul {
  transform: translateX(15px);
}
footer .container .wrapper .footer__article .footer__article-ul li {
  padding-bottom: 10px;
}
footer .container .wrapper .footer__article .footer__article-ul li::before {
  content: "•";
  color: #FFFFFA;
  font-weight: bold;
  display: inline-block;
  width: 1em;
  margin-left: -1em;
}
footer .container .wrapper .footer__article .footer__article-ul li a {
  color: #FFFFFA;
  text-decoration: none;
}
footer .container .wrapper .footer__article .footer__article-icons {
  display: flex;
  padding-top: 20px;
  gap: 30px;
  font-size: 2rem;
}
footer .footer__copyright {
  text-align: center;
  height: 40px;
  font-family: "Open Sans", sans-serif;
  font-size: 1rem;
  color: #FFFFFA;
  line-height: 40px;
  background-color: #4a4a49;
}
footer .footer__copyright .footer__copyright--span {
  color: #FF6600;
  font-family: "Open Sans", sans-serif;
}

.app__header .app__header__wrapper .app__logo {
  padding: 20px;
  text-decoration: none;
}
.app__header .app__header__wrapper .app__header__form_user {
  display: flex;
  justify-content: center;
  align-items: center;
}
.app__header .app__header__wrapper .app__header__form_user .user {
  display: inline-block;
  background-color: transparent;
  border: none;
  text-decoration: none;
}
.app__header .app__header__wrapper .app__header__form_user .name {
  font-size: 1.5rem;
  font-family: "Open Sans", sans-serif;
  color: #FFFFFA;
}
.app__header .app__header__wrapper .app__header__form_user i {
  font-size: 2rem;
  padding: 20px;
  color: #FF6600;
}

.app {
  width: 100vw;
  display: flex;
}
.app .app__sidebar--left {
  width: 170px;
  height: calc(100vh - 80px);
  background-color: #5B605F;
}
.app .app__sidebar--left ul {
  width: 100%;
  display: flex;
  flex-direction: column;
}
.app .app__sidebar--left ul li {
  display: flex;
  align-items: center;
  width: 100%;
  height: 50px;
  margin-bottom: 10px;
}
.app .app__sidebar--left ul li:first-child {
  margin-top: 10px;
}
.app .app__sidebar--left ul li i {
  display: none;
  font-size: 1.225rem;
  color: #FF6600;
  padding-right: 10px;
}
.app .app__sidebar--left ul li .show-icon {
  display: inline-block;
}
.app .app__sidebar--left ul li input {
  display: block;
  height: 100%;
  width: 90%;
  font-size: 1.225rem;
  font-family: "Open Sans", sans-serif;
  text-align: left;
  border: none;
  background-color: #5B605F;
  color: #A1A194;
  transition: 0.2s;
}
.app .app__sidebar--left ul li input:hover {
  border-left: 2px solid #FF6600;
  color: #FFFFFA;
}
.app .app__sidebar--left ul li .selected-input {
  border-left: 2px solid #FF6600;
  color: #FFFFFA;
}
.app .area_for_subsections {
  display: flex;
  justify-content: center;
  align-items: center;
  width: calc(100vw - 170px);
  height: calc(100vh - 80px);
  background-image: url("/images/background.png");
}
.app .area_for_subsections .subSec {
  display: none;
}
.app .area_for_subsections .show__subsection {
  display: flex;
}

.subSec__welcome {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 80%;
  height: 80%;
  border: 2px dotted grey;
  background-color: #FFFFFA;
}
.subSec__welcome .Message_and_form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.subSec__welcome .Message_and_form h1 {
  font-size: 30px;
  color: #4a4a49;
}

.subSec__welcome .Storing_and_adding_name {
  display: flex;
  flex-direction: column;
}

.subSec__desktop {
  flex-direction: column;
  width: 95%;
  height: 90%;
  border: 2px dotted grey;
  background-color: #FFFFFA;
}
.subSec__desktop .subtasks {
  display: flex;
  width: 100%;
}
.subSec__desktop .subtasks .Widget_add_recipes_and_plan {
  display: flex;
  width: 50%;
}
.subSec__desktop .subtasks .Widget_add_recipes_and_plan .square {
  width: 150px;
  height: 150px;
  margin: 10px;
  background-color: lightgreen;
}

.subSec__desktop .subtasks .Widget_info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 50%;
}
.subSec__desktop .subtasks .Widget_info .info {
  background-color: lightblue;
  height: 50px;
  width: 100%;
}

.subSec__desktop .Week_preview {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: lightpink;
  font-size: 20px;
  width: 100%;
  height: 100%;
}